*{
  font-family: 'Courier New', Courier, monospace;

}
body{
  background-color: #fffff3;
  color:#030301;
}

input{
  width: 50%;
  height: 24px;
}

h3{
  text-align: center;
}
.card-grid{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card{
  align-content: center;
  border: 1px solid black;
  margin-bottom: 10px;
  margin-right: 10px;
  width: 50%;
  overflow: hidden;
}

.gif-img{
  width: 200px;
  display:block;
  margin: 10px auto;
 
}

.img-title{
  margin: 10px;
  text-align: center;
}